import React from "react";
import PropTypes from "prop-types";
import Card from "../card";
import Message from "../message";
import Statistic from "../statistic";
import Button from "../button";
import Medal from "../medal";
import "./career-match.scss";

const CareerMatch = ({ 
  className, 
  title,
  matchRating,
  description,
  avatar,
  message,
  startingSalary,
  futureSalary,
  relevantStrengths
}) => (
  <Card className={`career-match ${className}`}>
    <header className="career-match__header">
      <div className="career-match__header-left">
        <Medal className="career-match__medal" position="1" />
        <h3 className="career-match__title g--margin-none">{title}</h3>
      </div>
      <span className="career-match__match-rating g--margin-none">
        {matchRating}% career match
      </span>
    </header>
    <hr />
    {description && (
      <>
        <p className="g--margin-none career-match__description">
          {description}...
          <b className="career-match__read-more"> continue reading</b>
        </p>
        <hr />
      </>
    )}
    {message && (
      <>
        <Message
          avatar={avatar}
          message={message}
        />
        <hr />
      </>
    )}
    {startingSalary || futureSalary || relevantStrengths ? 
      <>
        {startingSalary && (
          <Statistic 
            startingSalary={startingSalary}
          />
        )}
        {futureSalary && (
          <Statistic 
            futureSalary={futureSalary}
          />
        )}
        {relevantStrengths && (
          <Statistic 
            relevantStrengths={relevantStrengths}
          />
        )}
        <hr />
      </>
    : '' }
    <Button 
      label={`Read more ${title} stats`}
      icon="arrow-right"
    />
  </Card>
);

CareerMatch.defaultProps = {
  className: "",
  title: "",
  matchRating: null,
  description: "",
  avatar: "",
  message: "",
  startingSalary: null,
  futureSalary: null,
  relevantStrengths: null,
};

CareerMatch.propTypes = {
  className: PropTypes.string,
  title: PropTypes.string,
  matchRating: PropTypes.number,
  description: PropTypes.string,
  avatar: PropTypes.string,
  message: PropTypes.string,
  startingSalary: PropTypes.number,
  futureSalary: PropTypes.number,
  relevantStrengths: PropTypes.number,
};

export default CareerMatch;
